import React from "react";
import { create } from "jss";
import jssRTL from "jss-rtl";
import { connect } from "react-redux";
import { ConnectedRouter } from "connected-react-router";
import { SnackbarProvider } from "notistack";
import { history } from "./_store/store.config";
import AppRouter from "./router";
import "./App.css";
import {
  Box,
  createStyles,
  jssPreset,
  makeStyles,
  Paper,
  responsiveFontSizes,
  Slide,
  StylesProvider,
  Theme,
  ThemeProvider,
} from "@material-ui/core";
import LessonPlayerMode from "./components/Layout/Instruments/Piano/PlayMode/LessonPlayMode";
import { LightTheme, DarkTheme } from "./plugin/Themes";
import Breadcrumb from "./components/Layout/Breadcrumbs";
import Header from "./components/Layout/Header/Header";
import Notifier from "./components/Layout/SnackBar/Notifier";
import { AppState } from "./_types";

const jss = create({ plugins: [...jssPreset().plugins, jssRTL()] });

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    app: {
      width: "100vw",
      height: "100vh",
      overflowX: "hidden",
      display: "flex",
      flexDirection: "column",
      overflow: "hidden",
    },
    mainBody: {
      height: "inherit",
      overflowX: "hidden",
      overflowY: "auto",
    },
    paper: {
      zIndex: 999999,
      position: "absolute",
    },
  })
);

interface AppProps {
  playMode: boolean;
  theme: "light" | "dark";
}
type Props = AppProps & LinkDispatchProps;

const App: React.FC<Props> = ({ theme, playMode }) => {
  const classes = useStyles();
  const currentTheme = theme === "light" ? LightTheme : DarkTheme;
  const responsTheme = responsiveFontSizes(currentTheme);
  return (
    <ConnectedRouter history={history}>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={responsTheme}>
          <SnackbarProvider>
            <Slide direction="right" in={playMode} mountOnEnter unmountOnExit>
              <Paper elevation={4} className={classes.paper}>
                <LessonPlayerMode />
              </Paper>
            </Slide>
            <Box className={classes.app}>
              <Header />
              <Breadcrumb />
              <Notifier />
              <Box id="main-body" className={classes.mainBody}>
                <AppRouter />
              </Box>
            </Box>
          </SnackbarProvider>
        </ThemeProvider>
      </StylesProvider>
    </ConnectedRouter>
  );
};

interface LinkDispatchProps {}

const mapStateToProps = (state: AppState) => {
  const { theme } = state.auth;
  const { playMode } = state.melodition;
  return { theme, playMode };
};

const conectedApp = connect(mapStateToProps, {})(App);

export default conectedApp;
